/* eslint-disable react/jsx-no-bind */
/* eslint-disable radix */
import { h,Component } from 'preact';
import { useEffect, useState, useRef } from 'preact/hooks';
import ReactSlider from "react-slider";
import { formatNumberWithCommas } from '../../common/helpers';
import Select from '../../common/atoms/select';
import Input from '../../common/atoms/input';
import './style.css';
import 'react-hint/css/index.css';
import ReactHintFactory from 'react-hint';
const ReactHint = ReactHintFactory({createElement: h, Component});


export default () => {
	const [fundOptions, setFundOptions] = useState([]);
	const [selectedFund, setSelectedFund] = useState({});
	const [searchValue, setSearchValue] = useState('hdfc');
	const [state, setState] = useState({
		amount: 10000,
		stepUp: 10,
		duration: 5,
		returns: 11.25,
		currentTab: 1, // if 1 then its sip
		calculatedTotalInvestment: 0,
		calculatedWealthGained: 0,
		calculatedFinalValue: 0
	});
	const isFirstRun = useRef(true);

	const calcIcon = <svg width="18" height="20" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 18 20" id="calculator"><path d="M16.5 0h-15C.75 0 .25.5.25 1.25v17.5C.25 19.5.75 20 1.5 20h15c.75 0 1.25-.5 1.25-1.25V1.25C17.75.5 17.25 0 16.5 0zM5.25 15a1.25 1.25 0 11-2.5 0 1.25 1.25 0 012.5 0zm0-5a1.25 1.25 0 11-2.5 0 1.25 1.25 0 012.5 0zm5 5a1.25 1.25 0 11-2.5 0 1.25 1.25 0 012.5 0zm0-5a1.25 1.25 0 11-2.5 0 1.25 1.25 0 012.5 0zm5 5.25a1 1 0 01-1 1h-.5a1 1 0 01-1-1v-5.5a1 1 0 011-1h.5a1 1 0 011 1v5.5zm0-10a1 1 0 01-1 1H3.75a1 1 0 01-1-1V3.5a1 1 0 011-1h10.5a1 1 0 011 1v1.75z" fill="#11C095" /></svg>;

	const infoIcon = <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
		<path fill-rule="evenodd" clip-rule="evenodd" d="M6 12C9.31371 12 12 9.31371 12 6C12 2.68629 9.31371 0 6 0C2.68629 0 0 2.68629 0 6C0 9.31371 2.68629 12 6 12ZM7 3.00027C7 3.46152 6.6625 3.80731 6.115 3.80731C5.5675 3.80731 5.23375 3.46152 5.23375 3.00027C5.23375 2.53902 5.57125 2.19043 6.115 2.19043C6.6625 2.19043 7 2.53902 7 3.00027ZM8.305 8.99985H4V7.9161H5.4925V5.37735H4V4.2936H6.8425V7.9161H8.305V8.99985Z" fill="#B3B3B3"/>
	</svg>;

	useEffect(() => {
		getOptionsValues();
	}, []);

	useEffect(() => {
		const result = sipComputationWithStepup(
			state.amount,
			state.returns,
			state.duration,
			state.stepUp,
			state.currentTab === 1
		);

		if (state.currentTab !== 1) {
			// we are in target amount
			setState({
				...state,
				calculatedTotalInvestment: result[0] ? Math.ceil(result[0]) : 0,
				calculatedWealthGained: result[1] ? Math.ceil(result[1]) : 0,
				calculatedFinalValue: result[2] ? Math.ceil(result[2]) : 0
			});
		} else {
			// we are in sip
			setState({
				...state,
				calculatedTotalInvestment: result[0] ? Math.ceil(result[0]) : 0,
				calculatedWealthGained: result[1] ? Math.ceil(result[1]) : 0,
				calculatedFinalValue: result[2] ? Math.ceil(result[2]) : 0
			});
		}
	}, [
		state.amount,
		state.stepUp,
		state.duration,
		state.returns,
		state.currentTab
	]);

	useEffect(() => {
		setState({
			...state,
			returns: parseFloat(selectedFund && selectedFund.returnValue)
		});
	}, [selectedFund]);

	useEffect(() => {
		if (isFirstRun.current) {
			isFirstRun.current = false;
			return;
		}

		const timeout = setTimeout(() => {
			if (searchValue)
				 getOptionsValues(searchValue);
		}, 600);

		return () => clearTimeout(timeout);
	},
	[searchValue]
	);

	const stepUpOptions = [
		{
			name: '0 %',
			value: 0
		},
		{
			name: '10 %',
			value: 10
		},
		{
			name: '15 %',
			value: 15
		},
		{
			name: '20 %',
			value: 20
		},
		{
			name: '25 %',
			value: 25
		},
		{
			name: '30 %',
			value: 30
		}
	];

	const getOptionsValues = (query = 'hdfc') => {
		fetch('https://apus.scripbox.com/api/v1/search', {
			method: 'post',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				q: query,
				from: 0,
				size: 10,
				skip_filter: true,
				sort_by: "tag_priority",
				includes :[
					"fund_name",
					"asset_class",
					"return3y",
					"return5y",
					"slug"
				]
			})
		})
			.then(res => res.json())
			.then(
				(data) => {
					let result = [];
					data.results.map(eachData => {
						result.push({
							name: eachData._source.fund_name,
							value: eachData._source.fund_name,
							returnValue:
									eachData._source.asset_class === "Debt"
										? eachData._source.return3y
										: eachData._source.return5y,
							slug: eachData._source.slug
						});
					});
					setFundOptions(result);
					setSelectedFund(result[0]);
					return result;
				},
				(error) => {
					console.log(error);
				}
			);
	};

	const handleAmountChange = (e) => {
		setState({
			...state,
			amount: e.target.value
		});
	};

	const handleStepUpChange = (value) => {
		setState({ ...state, stepUp: parseFloat(value) });
	};

	function sipComputationWithStepup(
		investments,
		rate,
		tenure,
		stepup_percentage,
		isSip
	) {
		//with sip stepup percentage
		let totalEarnings, totalInvestment, monthlyInvestments, futureValue, result;
		let rate_per_month = parseFloat(rate) / (12 * 100);
		let no_investment_months = parseInt(tenure) * 12;
		stepup_percentage = stepup_percentage || 10;
		stepup_percentage = stepup_percentage / 100;
		let T1 = Math.pow(1 + rate_per_month, 12);
		let T2 = (1 + stepup_percentage) * Math.pow(1 + rate_per_month, -12);
		let T3 = (T1 - 1) / rate_per_month / (T2 - 1);
		let T4 =
				T3 *
				Math.pow(1 + rate_per_month, no_investment_months - 11) *
				(Math.pow(T2, no_investment_months / 12) - 1);
		if (!isSip) {
			futureValue = investments;
			monthlyInvestments = futureValue / T4;
			if (stepup_percentage) {
				let sipAmount = monthlyInvestments;
				for (let i = 0; i < tenure; i++) {
					totalInvestment =
							parseInt(totalInvestment ? totalInvestment : 0) +
							parseInt(sipAmount * 12);
					sipAmount =
							parseInt(sipAmount) + parseInt(sipAmount * stepup_percentage);
				}
			} else {
				totalInvestment = no_investment_months * monthlyInvestments;
			}
			totalEarnings = futureValue - totalInvestment;
			result = [investments, totalEarnings, monthlyInvestments];
		} else if (isSip) {
			monthlyInvestments = investments;
			futureValue = monthlyInvestments * T4;
			if (stepup_percentage) {
				let sipAmount = monthlyInvestments;
				for (let j = 0; j < tenure; j++) {
					totalInvestment =
							parseInt(totalInvestment ? totalInvestment : 0) +
							parseInt(sipAmount * 12);
					sipAmount =
							parseInt(sipAmount) + parseInt(sipAmount * stepup_percentage);
				}
			} else {
				totalInvestment = no_investment_months * monthlyInvestments;
			}
			totalEarnings = futureValue - totalInvestment;
			result = [totalInvestment, totalEarnings, futureValue, monthlyInvestments];
		}
		return result;
	}

	const handleOptionChange = (value, restValues) => {
		setSelectedFund(restValues);
	};

	const handleSearchOptions = (query) => {
		setSearchValue(query);
	};

	const handleTabChange = (e) => {
		if (+ e.target.value === 2){
			setState({
				...state,
				amount: 100000,
				currentTab: + e.target.value
			});
		}
		else {
			setState({
				...state,
				currentTab: + e.target.value,
				amount: 10000
			});
		}
	};

	const handleYearChange = (e) => {
		if (+e.target.value <= 30){
			setState({
				...state,
				duration: + e.target.value
			});
		}
		else {
			setState({
				...state,
				duration: 30
			});
		}
	};
	return (
		<div className="w-350 bg-white shadow-calculator rounded-md mx-auto mt-5 font-inter">
			<div className="px-4 py-6">
				<div className="relative flex items-center">
					<figure className="flex justify-center items-center m-0 p-0">
					 {calcIcon}
					</figure>
					<h1 id="calc" className="h-5 font-medium text-base leading-5 pl-10.25 text-595959 not-italic ">Investment Calculator</h1>
				</div>

				<div className={'tab-control'}>
					<span className={state.currentTab === 1 ? "selection" : "selection1"} />

					<div className={"option"}>
						<input type="radio" id="apple-music" name="sample" value={1} onChange={handleTabChange} checked={state.currentTab === 1} />
						<label for="apple-music"><span className={state.currentTab === 1 ? "activeLabel" : "normalLabel"}>Monthly SIP</span></label>
					</div>

					<div className={"option"}>
						<input type="radio" id="your-library" name="sample" value={2} onChange={handleTabChange} checked={state.currentTab === 2} />
						<label for="your-library"><span className={state.currentTab === 2 ? "activeLabel" : "normalLabel"}>Target Amount</span></label>
					</div>

				</div>

				<div className="pt-16 pb-6">
					<label className="pb-2 font-small text-sm leading-4 flex items-center text-595959 not-italic">Fund Name</label>
					<div className="flex flex-col static bg-white rounded-md flex-none order-none self-stretch flex-grow-0 m-0">
						<Select options={fundOptions} placeholder={"Search"} isSearch={true} handleOptionChange={handleOptionChange} value={selectedFund.value} handleSearchOptions={handleSearchOptions} />
					</div>
				</div>

				<div className="flex">
					<div className="w-full max-w-full pr-3">
						<label className="w-52 h-4 font-small text-sm leading-4 flex items-center text-595959 not-italic  pb-2">Amount</label>
						<Input className="w-205 h-8 shadow-input rounded-md py-1 pr-2 pl-6 bg-rupee bg-no-repeat bg-left-12-top-10 text-sm" type="number" value={state.amount} onChange={handleAmountChange} />
					</div>
					<div className="w-full">
						<label className="relative font-small text-sm leading-4  flex items-center text-595959 not-italic  pb-2 h-4">
							Step-up
							<span id="step" className="pl-1 cursor-pointer " data-rh="The amount by which your SIP will be increased" data-rh-at="bottom" >
								<figure>
									{infoIcon}
								</figure>
							</span>
						</label>
						<Select options={stepUpOptions} placeholder={'Choose step up'} value={state.stepUp} handleOptionChange={handleStepUpChange} />
					</div>
				</div>

				<div className="py-6 ">
					<p className="font-small text-sm leading-4 flex items-center text-595959 not-italic pb-2 flex justify-between ">Investment Duration <span className="text-4E93EE ml-1">
						<input id="year" type="number" className="w-5 text-center hover:border-4 border-black"
						 value={state.duration} maxLength={2} onChange={handleYearChange} />
						<span>years</span></span>
					</p>
					<div className="pt-4" style={{ position: "relative" }}>
						<ReactSlider
							min={0}
							defaultValue={[5]}
							step={1}
							value = {state.duration}
							onChange={val => setState({ ...state, duration: val })}
							className={"horizontalSlider"}
							trackClassName={`${"track"}`}
							thumbClassName={"thumb"}
							max={30} />
					</div>
				</div>

				<div className="flex justify-between items-center">
					<div>
						<p className="text-lg leading-5 text-181818 pb-2">{formatNumberWithCommas(state.calculatedTotalInvestment)}</p>
						<span className="text-sm text-737373">Total Investment</span>
					</div>
					<div>
						<p className="text-lg leading-5 text-181818 pb-2">{formatNumberWithCommas(state.calculatedWealthGained)}</p>
						<span className="text-sm text-737373">Wealth Gained</span>
					</div>
				</div>

				<div className="mt-4 mb-2 p-5 rounded-lg text-center bg-grey ">
					<p className="font-bold text-2xl text-0FAC85"> {state.calculatedFinalValue >= 0 ? formatNumberWithCommas(state.calculatedFinalValue) : 0}</p>
					<p className="text-04040 text-sm">Total Corpus Created (₹)</p>
					<small className="text-xs text-737373">With assumed returns of {state.returns} %</small>
				</div>

				<div className="outline-none ">
					<a className="w-full bg-green-500 hover:bg-green-600 rounded-md text-center border-none text-sm inline-block text-white py-3 font-semibold" href="https://scripbox.com/mutual-fund/hdfc-index-fund-nifty-50-plan-growth-plan">See Scripbox Opinion for this fund</a>
				</div>
			</div>
			 <ReactHint events  className={"toolTip"} />
		</div>

	);
};
