import { h } from 'preact';
import SelectSearch from 'react-select-search';

const Select = ({ options,placeholder,isSearch,value,handleOptionChange,className,handleSearchOptions }) => {
	return (
		<div className="w-full px-0.3 shadow-input rounded-md">
			<SelectSearch options={options} getOptions={handleSearchOptions} search={isSearch} value={value} name="fund" placeholder={placeholder} onChange={handleOptionChange} />
		</div>
	);
};

export default Select;
